<script>
import { defineComponent } from 'vue'
import FullCalendar from '@fullcalendar/vue3'
import dayGridPlugin from '@fullcalendar/daygrid'
import timeGridPlugin from '@fullcalendar/timegrid'
import interactionPlugin from '@fullcalendar/interaction'
import listPlugin from '@fullcalendar/list'
import axios from 'axios';

export default defineComponent({
  components: {
    FullCalendar,
  },

  data () {
    return {
      currentEvents: [], // db event를 가공해서 저장할 배열
      key: '',
      calendarOptions: {
        plugins: [
          dayGridPlugin,
          timeGridPlugin,
          interactionPlugin,
          listPlugin
        ],
        headerToolbar: {
          left: 'prev,next today',
          center: 'title',
          right: 'dayGridMonth,timeGridWeek,timeGridDay,listWeek'
        },
        initialView: 'dayGridMonth',
        events: [], // calendar에 뿌려줄 배열
        editable: false, // 일정 드래그 방지
        selectable: true,
        selectMirror: true,
        dayMaxEvents: true,
        weekends: true,
        select: this.handleDateSelect,
        eventClick: this.handleEventClick,
        eventsSet: this.setEvents,
      },
    }
  },

  mounted() {
    this.checkInput();
  },

  methods: {
    checkInput(){ // tKey로 login 판별
      console.log('checkInput');
      if(document.getElementsByName('tKey').length > 0  && document.getElementsByName('tKey')[0].value){
        console.log(document.getElementsByName('tKey')[0].value);
        this.getTodo(document.getElementsByName('tKey')[0].value);
      }else{
        console.log('not found input');
      }
      // this.forTest(this.key);
    },
    forTest(temp){ // 개발할 때 임시로 사용
      console.log("key : " + temp);
      this.key = temp;
      this.eventsList();
    },
    getTodo(value){
      this.key = value;
      this.eventsList();

    },
    eventsList: function() {
      var dataList = this.currentEvents
      axios.get("http://localhost:8056/api/project?t_key="+this.key)
      .then(
        (res) => {
          console.log(">>>>>>> axios success", res.data)
          res.data.map(function(element) {
            // 날짜 데이터 - 시간 제거
            const startDate = new Date(element.start_date);
            startDate.setHours(0, 0, 0, 0);
            const endDate = new Date(element.due_date);
            endDate.setHours(0, 0, 0, 0);
            startDate.setDate(startDate.getDate() + 1);
            endDate.setDate(endDate.getDate() + 1);

            dataList.push({ // dataList에 mapping 후 저장 - currentEvents
              id: element.project_num,
              title: element.project_title,
              start: startDate.toISOString().split('T')[0],
              end: endDate.toISOString().split('T')[0],
              pm: element.project_manager,
              member_num: element.member_num 
            })
          })
        },
        console.log("currentEvents >>>>>>>", this.currentEvents)
      ).catch(
        (err) => {
          console.log(err)
        }
      )
    },
    setEvents() { // 가공된 currentEvents와 calendar에 뿌려줄 events 연결
      this.calendarOptions.events = this.currentEvents;
    },
    handleWeekendsToggle() {
      this.calendarOptions.weekends = !this.calendarOptions.weekends // update a property
    },
    handleDateSelect(selectInfo) {
      let title = prompt('일정 제목을 입력하세요')
      let calendarApi = selectInfo.view.calendar

      calendarApi.unselect() // clear date selection

      if (title) {
        calendarApi.addEvent({
          id: selectInfo.id,
          title,
          start: selectInfo.startStr,
          end: selectInfo.endStr,
          allDay: selectInfo.allDay
        })
      }
    },
    handleEventClick(clickInfo) {
      if (confirm(`'${clickInfo.event.title}' 일정을 삭제하시겠습니까?`)) {
        clickInfo.event.remove()
      }
    },
  },
})
</script>

<template>
  <div class='app'>
    <div class='app-sidebar'>
      <div class='app-sidebar-section'>
        <!-- <h2>프로젝트 관리</h2> -->
      </div>
      <div class='app-sidebar-section'>
        <label>
          <input
            type='checkbox'
            :checked='calendarOptions.weekends'
            @change='handleWeekendsToggle'
          />
          주말 포함
        </label>
      </div>
      <div class='app-sidebar-section'>
        <h2>프로젝트 관리 ({{ currentEvents.length }})</h2>
        <ul>
          <li v-for='event in currentEvents' :key='event.id'>
            <span>{{ event.title }}</span><br>
            <b>{{ event.start }}</b>
          </li>
        </ul>
      </div>
    </div>
    <div class='app-main'>
      <FullCalendar
        class='app-calendar'
        :options='calendarOptions'
      >
        <template v-slot:eventContent='arg'>
        <!-- <b>{{ arg.timeText }}</b> -->
          <span>{{ arg.event.title }}</span>
        </template>
      </FullCalendar>
    </div>
  </div>
</template>

